body { font-family: "Open Sans Condensed",sans-serif; height: 100%; overflow: hidden; }
h1,h2,h3,h4,h5,h6,input,select,textarea { font-family: "Open Sans Condensed",sans-serif; margin: 0; }
*, *:before, *:after { -webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box; }
.row { max-width: 100%; width: 100%; }
.columns { padding: 0; }
a { color: #4298b5; text-decoration: none; }
.hide-this { display: none; }


/* GENERAL */
#app-panel { height: 100%; left: 0; position: absolute; top: 0; transition: left .55s; width: 100%; }
#app-panel.menu-opened { left: -350px; }
.panel-header { background-color: #4298b5; color: #fff; padding: 8px 16px; text-align: left; }
.panel-header img { height: 36px; margin-right: 8px; vertical-align: middle; }
.panel-header h2 { color: #fff; display: inline-block; font-size: 1.8em; vertical-align: middle; }
.panel-header a { border: 1px solid #fff; color: #fff; display: inline-block; font-size: .8em; margin-left: 18px; margin-top: 4px; padding: 0 4px; vertical-align: middle; }
.panel-header span { font-size: .85em; margin-left: 14px; vertical-align: middle; }
span.panel-close { color: #fff; cursor: pointer; font-size: 2.4em; position: absolute; top: 74px; right: 16px; }
.half-column { display: inline-block; vertical-align: top; width: 50%; }
.limit-height { height: 200px; overflow-y: auto; }
.red-button { background-color: #c8102e; border: #c8102e; color: #fff; font-size: .85em; float: right; margin-top: 13px; padding: 4px 8px 4px 5px; }
.red-button img { height: 20px; margin-right: 2px; vertical-align: top;  }
.red-button:hover { background-color: #4298b5; color: #fff; }
.checkbox { color: #000; display: inline-block; font-size: .9em; margin: 0 18px 0 4px; }
.checkbox span { background-color: #fff; border: 1px solid #000; display: inline-block; height: 14px; margin-right: 6px; vertical-align: middle; width: 14px; }
.checkbox span img { display: none; left: -2px; position: relative; top: -5px; width: 16px; }
.checkbox.checked span img { display: block; }
#body-container { background-color: #303; height: 100%; left: 0; 
    /* padding-top: 72px; */
    position: absolute; top: 0; width: 100%; }


/* TOP BAR */
#top-bar { background-color: #fff; height: 72px; position: relative; z-index: 2; }
#top-bar > a { display: inline-block; vertical-align: middle; }
#top-bar > a > img { margin: 11px 24px; width: 80px; }
#breadcrumb-menu { display: inline-block; margin-left: 9px; vertical-align: middle; }
#breadcrumb-menu a { display: inline-block; vertical-align: middle; }
#breadcrumb-menu a img { width: 28px; }
#breadcrumb-menu span { color: #a8a99e; font-size: .8em; font-weight: 700; margin: 0 8px 0 6px;  }
#breadcrumb-menu h1 { display: inline-block; font-size: 1em; }
#top-right { float: right; margin: 22px 26px; }
#top-right a { cursor: pointer; }
#project-drop-down { display: inline-block; }
#project-drop-down span { text-decoration: underline; margin-right: 6px; }
#project-drop-down img { width: 20px; }
#menu-button { cursor: pointer; display: inline-block; margin-left: 42px; vertical-align: middle; }
#menu-button span { background-color: #54595b; border-radius: 3px; display: block; height: 4px; margin-bottom: 6px; transition: transform .1s; width: 44px; }
.menu-opened #menu-button span { margin-top: 10px; transform: rotate(45deg); }
.menu-opened #menu-button span:nth-of-type(2) { margin-top: -10px; transform: rotate(-45deg); }
.menu-opened #menu-button span:nth-of-type(3) { display: none; }

/* LEFT BAR & PANELS */
#left-bar { background-color: #4298b5; height: 100%; left: 0; padding-top: 14px; position: absolute; text-align: center; top: 72px; width: 80px; }
#left-bar a { color: #fff; display: block; font-size: .9em; margin-bottom: 14px; position: relative; }
#left-bar a:after { background-color: #c8102e; content: ""; display: block; height: 100%; left: -10px; position: absolute; top: 0; transition: left .65s; width: 10px; }
#left-bar a.selected:after { left: 0; }
#left-bar a img { display: block; height: 40px; margin: 0 auto; }
#left-bar-panel { background-color: #66acc3; height: 100%; left: -240px; padding: 79px 18px 16px; position: absolute; top: 0; transition: left .65s; width: 320px; }
#left-bar-panel.opened { left: 80px; }
#left-bar-panel span.panel-close { color: #fff; cursor: pointer; font-size: 2.4em; position: absolute; top: 72px; right: 14px;  }
#left-bar-panel h2 { color: #fff; font-size: 1.8em; font-weight: 700; margin-bottom: 18px; }

.panel-input { margin-bottom: 12px; position: relative; width: 100%; }
.panel-input.shorter { width: 75%; }
.panel-input.shortest { width: 26%; }
.wider .panel-input.shortest { width: 20%; }
.panel-input label { color: #fff; display: block; font-size: .9em; line-height: 1.2em; }
.panel-input input[type="text"],
.panel-input select { background-color: transparent; display: block; font-size: 1em; height: 28px; margin-top: 2px; padding-left: 4px; width: 100%; }
#left-bar-panel .panel-input input[type="text"],
#left-bar-panel .panel-input select { background-color: #fff; }
.panel-input textarea { display: block; font-size: 1em; height: 84px; padding: 3px 5px; resize: none; width: 100%; }
.panel-input img { bottom: 3px; height: 21px; position: absolute; right: 4px; }
.same-row-inputs .panel-input { display: inline-block; margin-right: 25px; }
.same-row-inputs .panel-input.shorter { width: 46%; }
.required { color: #a00; font-size: .8em; font-style: italic; margin-right: 5px; }
input[type="checkbox"] { margin-left: 6px; }
input[type="file"] { margin: 2px 0 16px; }

#legend-panel .checkbox { font-size: 1em; font-weight: 700; margin-bottom: 12px; margin-left: 0;}
#legend-panel .checkbox span { height: 18px; margin-right: 8px; width: 18px; }

.basemap-link { color: #fff; display: inline-block; font-weight: 700; line-height: 1.1em; margin-bottom: 15px; margin-right: 8%; width: 41%; }
.basemap-link img { border: 1px solid #fff; width: 100%; }
.basemap-link.selected img { border: 2px solid #c8102e; }


/* RIGHT BAR & PANELS */
#right-bar { background-color: #fff; height: 100%; overflow: auto; padding-top: 72px; position: absolute; right: 0; top: 0; width: 38%; max-width: 550px !important; }
.right-panel-content { padding: 0 20px 80px; }
.right-panel-content h3 { clear: both; font-size: 1.3em; font-weight: 700; padding: 16px 0 12px; }
.right-panel-content p { line-height: 1.4em; margin-bottom: 14px; }
.option-list > a, .option-list-two-options div { border-bottom: 1px solid #a8a99e; display: block; font-size: .9em; padding: 4px 2px; width: 100%; }
.option-list > a:last-of-type { border-bottom-width: 3px; }
.option-list a img { height: 18px; margin-right: 4px; vertical-align: middle; }
.option-list a span { color: #000; float: right; }
.option-list-two-options div a:last-of-type { float: right; }
.contact-info { border-bottom: 1px solid #555; }
.contact-info a img { height: 22px; margin-left: 4px; vertical-align: middle; }

#parcel-progress { background-color: #e4e4e4; padding: 19px 0 0; }
#parcel-progress ul { border-top: 1px solid #000; margin: 0 10%; position: relative; }
#parcel-progress ul li { display: inline-block; font-size: .9em; left: -10%; line-height: 1.3em; position: absolute; text-align: center; top: -18px; width: 19%; }
#parcel-progress ul li:nth-of-type(2) { left: 15%; }    /* modified in denver water */
#parcel-progress ul li:nth-of-type(3) { left: 40%; }
#parcel-progress ul li:nth-of-type(4) { left: 65%; }
#parcel-progress ul li:nth-of-type(5) { left: 90%; }
#parcel-progress ul li:nth-of-type(6) { left: 74%; }
#parcel-progress ul li:nth-of-type(7) { left: auto; right: -10%; }
#parcel-progress ul li span { background-color: #a8a99e; border-radius: 100%; display: block; height: 34px; margin: 0 auto 4px; width: 34px; }
#parcel-progress ul li span img { display: none; }
#parcel-progress ul li span.checked { background-color: #c8102e; padding-top: 6px; }
#parcel-progress ul li span.checked img { display: inline-block; }
#parcel-progress ul li span.checked.rejected { background-color: #000; }

#parcels-view-on-map { background-color: #e4e4e4; margin-bottom: 12px; padding: 4px 5px 5px; }
#parcels-view-on-map img { margin-right: 3px; vertical-align: middle; }
#parcels-view-on-map strong { margin-right: 14px; vertical-align: middle;  }
#parcels-view-on-map a.checkbox { display: inline-block; }
.owner-parcel-list a { display: inline-block; line-height: 1.4em; margin-right: 35px; }

.compensation-box { background-color: #e4e4e4; height: 120px; margin-right: 4%; margin-bottom: 14px; padding: 9px 11px 10px; position: relative; width: 48%; }
.compensation-box:nth-of-type(even) { margin: 0; }
.compensation-box h4 { font-size: .9em; font-weight: 700; }
.compensation-box img { background-color: #c8102e; padding: 2px 4px; position: absolute; right: 0; top: 0; }
.compensation-box span { font-size: 4em; line-height: 1.2em; }
.compensation-box span.not-entered { display: block; font-size: .9em; font-style: italic; margin-top: 28px; }

#stat-numbers { background-color: #e4e4e4; padding: 18px 0 30px; }
#stat-numbers div { display: inline-block; text-align: center; width: 50%; }
#stat-numbers div img { height: 30px; }
#stat-numbers div h2 { font-size: 1.1em; font-weight: 700; padding: 0; }
#stat-numbers div span { color: #c8102e; font-size: 4.4em; line-height: 1.1em; }
#stat-graphs { margin-bottom: 40px; text-align: center; }
#stat-graphs h3 { font-size: 1.6em; margin: 8px 0; }
#parcel-status-graph { /*border-radius: 100%; */ display: inline-block; height: 220px; margin: 12px 18px 16px; overflow: hidden; position: relative; width: 220px; }
#parcel-status-graph p { height: 100%; width: 100%; margin: 0; position: absolute; top: 0; left: 0; }
#parcel-status-graph p > span { height: 50%; left: 0; position: absolute; top: 0; width: 50%; }
#roe-status-graph { /* border-radius: 100%; */ display: inline-block; height: 220px; margin: 12px 18px 16px; overflow: hidden; position: relative; width: 220px; }
#roe-status-graph p { height: 100%; width: 100%; margin: 0; position: absolute; top: 0; left: 0; }
#roe-status-graph p > span { height: 50%; left: 0; position: absolute; top: 0; width: 50%; }

.graph-key { display: inline-block; margin-top: 42px; vertical-align: top; width: 230px !important;  }
.graph-key > span { display: block; font-size: .95em; margin: 0 10px 6px; text-align: left; }
.graph-key > span > span { background-color: #e60000; display: inline-block; height: 16px; margin-right: 5px;  vertical-align: middle; width: 16px; }
#no-activity-gk > span { background-color: #ccc; }
#roe-in-progress-gk > span { background-color: #0070ff; }
#roe-obtained-gk > span { background-color: #5f0; }
#roe-with-conditions-gk > span { background-color: #ff0; }

/* MENU */
#menu-panel { background-color: #76797b; height: 100%; position: absolute; right: -350px; top: 0; width: 350px; }
#menu-panel .panel-header { background-color: #65696b; }
#menu-panel ul { list-style-type: none; margin: 0; padding: 0 }
#menu-panel ul li a { border-bottom: 1px solid #65696b; color: #fff; display: block; font-size: 1.2em; padding: 9px 14px; width: 100%; }
#menu-panel-bottom { bottom: 0; height: 52px; position: absolute; width: 100%; }
#menu-panel-bottom a { background-color: #c8102e; color: #fff; float: left; height: 100%; padding-left: 20px; padding-top: 13px; width: 65%; }
#menu-panel-bottom a img { height: 28px; margin-right: 8px; vertical-align: middle; }
#menu-panel-bottom a.logout-btn { background-color: #424242; padding-left: 0; text-align: center; width: 35%; }


/* OVERLAY SCREENS */
#overlay-screen { height: 100%; left: -100%; opacity: 0; position: fixed; text-align: center; top: 0; transition: opacity .5s, left 0s ease-in-out .5s; width: 100%; z-index: 3; }
#overlay-screen strong { font-size: .9em; }
#overlay-screen > span { background-color: #000; background-color: rgba(0,0,0,.4); cursor: pointer; height: 100%; left: 0; position: absolute; top: 0; width: 100%; }
#overlay-screen > div { background-color: #fff; display: inline-block; height: auto; margin: -24px auto 0; position: relative; vertical-align: middle; width: 56%; }
#overlay-screen > div.shorter { width: 35%; }
#overlay-screen .panel-close { top: 1px; }
#overlay-screen:after { content: ""; display: inline-block; height: 100%; vertical-align: middle; width: 0; }
#overlay-screen.showing { display: block; left: 0; opacity: 1; transition: left 0s, opacity .25s ease-in-out .1s; }
#overlay-screen.wider > div { width: 70%; }
.date-row p { display: inline-block; margin-right: 1%; width: 30%; }

.oc-header { border-bottom: 1px solid #000; padding-bottom: 6px; position: relative; }
.oc-header h3 { font-size: 1.2em; font-weight: 700; }
.oc-header > span { position: absolute; right: 0; top: 2px; }

#overlay-screen .panel-input label { color: #000; }
#overlay-screen a[class*="select-"] { display: inline-block; font-size: .85em; line-height: .9em; padding-bottom: 10px; }
#overlay-screen > div p { margin-top: 2px; }
.overlay-form-buttons { float: right; padding-bottom: 18px; }
.overlay-form-buttons a { margin-left: 16px; }

.overlay-kill-buttons { float: left; padding-bottom: 18px; margin-top: 14px; }
.overlay-kill-buttons a { margin-right: 16px; color: red; }

.document-links a { background-color: #e4e4e4; display: block; font-size: 1.4em; font-weight: 700; margin-bottom: 12px; padding: 4px 8px; width: 60%; }
.document-links a img { height: 34px; margin-right: 5px; vertical-align: middle; }
.document-receiver { background-color: #e4e4e4; border: 2px dashed #000; margin-bottom: 12px; padding: 6px 0 8px; text-align: center; width: 100%; }
.document-receiver img { display: block; height: 40px; margin: 0 auto; }
.document-receiver strong { display: block; margin-bottom: 8px; }
.document-receiver span { font-size: .85em; }
.half-column .document-receiver { margin-top: 4px; padding-top: 3px; width: 93%; }
.half-column .document-receiver img { height: 25px; }
.related-contacts a { margin-right: 44px; }

.overlay-container { padding: 16px 22px 26px; text-align: left; }
.overlay-container .red-button { display: inline-block; float: none; margin-top: 14px; }
.overlay-container .option-list a:last-of-type { border-width: 0; }

.react-datepicker-wrapper, .react-datepicker__input-container { width: 100%; }
.react-datepicker-popper { width: 600px; }
.react-datepicker { padding: 0 30px; }
.react-datepicker__navigation {border-width: 14px; height: 0; top: 70px; }
.react-datepicker__navigation--previous { left: -7px; }
.react-datepicker__navigation--next { right: -7px !important; }


#gray-gk > span { background-color: #b2b2b2; }
#orange-gk > span { background-color: #ffaa00; }
#pink-gk > span { background-color: #ff73df; }
#blue-gk > span { background-color: #0046a8; }
#green-gk > span { background-color: #5f0; }
#yellow-gk > span { background-color: #ffff00; }
#red-gk > span { background-color: #e60000; }
#purple-gk > span { background-color: #c500ff}


.esri-view .esri-view-surface--inset-outline:focus::after
{
    outline: none !important;
    content:"";
    box-sizing:border-box;
    position:absolute;
    z-index:999;
    top:0;
    left:0;
    height:100%;
    width:100%;
    pointer-events:none;
    overflow:hidden
} 